/*
This is how css architecture is organized https://github.com/inuitcss/inuitcss
For naming convention we use BEM.

/settings: Global variables, site-wide settings, config switches, etc.
/tools: Site-wide mixins and functions.
/generic: Low-specificity, far-reaching rulesets (e.g. resets).
/elements: Unclassed HTML elements (e.g. a {}, blockquote {}, address {}).
/objects: Objects, abstractions, and design patterns (e.g. .o-layout {}).
/components: Discrete, complete chunks of UI (e.g. .carousel {}). This is the one layer that inuitcss doesn’t provide code for, as this is completely your terrain.
/utilities: High-specificity, very explicit selectors. Overrides and helper classes (e.g. .u-hidden {}).
*/
