  border-bottom: 1px solid #eff0f0; }

.lane-filter__entry,
[class^="lane-filter__entry--"],
[class*=" lane-filter__entry--"] {
  font-size: 1.3rem;
  border-bottom: 1px solid #edeeef; }

.lane-filter__entry--winning {
